.App {
  padding: 5%;
}

.me-style {
  margin-left: 70px;
  position: fixed;
  text-align: right;
  top: 0px;
  left: 50%;
  height:100%;
}

.App-header {
  
  background-color: #222;
  height: 150px;
  padding: 10px;
  color: white;
}

.App-title {
  text-align: center;
  padding-right: 120px;
  color: #031926;
  font-size: 75px;
  font-family: 'Playfair Display', serif;
  height: 75px;
}

.App-intro {
  position: relative;
  font-size: 18pt;
  font-family: 'Lato', sans-serif;
  text-align: center;
  padding-right: 120px;

}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



